
document.addEventListener('DOMContentLoaded', ()=> {
    iniciarApp();
})

function iniciarApp () {
    navegacionFija();
    crearGaleria();
    scrollNav();

};

function navegacionFija () {
    const barra = document.querySelector('.header'); // lo q qeremos fijar
    const sobreFestival = document.querySelector('.sobre-festival') // en q momento qeremos q aparezca
    const body = document.querySelector('body');

    window.addEventListener('scroll', () => {
        if (sobreFestival.getBoundingClientRect().bottom < 0) {
            barra.classList.add('fijo'); // cuando llega a esa altura le agregamos la clase 
            body.classList.add('body-scroll');
        } else {
            barra.classList.remove('fijo'); // cuando no llegamos se la sacamos. 
            body.classList.remove('body-scroll');
        }  
    })
};

function scrollNav () {
    const enlaces = document.querySelectorAll('.navegacion-principal a') // selecciono los 3 <a> que dirigen a las secciones
    enlaces.forEach(enlace => { // hay q iterar para ir agregando los event listener. no se puede a todos con 1 asociarlos. 
        enlace.addEventListener('click', function (e) {
            e.preventDefault();
            const sectionScroll = e.target.attributes.href.value;
            const seccion = document.querySelector(sectionScroll);
            seccion.scrollIntoView({behavior: 'smooth'});            
        })
    })  
}

function crearGaleria () {
    const galeria = document.querySelector('.galeria-imagenes');

    for(let i = 1; i <= 12; i++) {
        const imagen = document.createElement('picture') // crea las 12 pictures. tags picture
        imagen.innerHTML = `<img loading="lazy" width='200' height='200' src="./img/thumb/${i}.jpg" alt="imagen-galeria">`;

        imagen.onclick = () => mostrarImagen(i);

        galeria.appendChild(imagen)
    }
}

function mostrarImagen(id) {
    const imagen = document.createElement('picture')
    imagen.innerHTML = `<img loading="lazy" src="./img/grande/${id}.jpg" alt="imagen-galeria">`;
// crea el overlay con la imagen
    const overlay = document.createElement('DIV');
    overlay.appendChild(imagen);
    overlay.classList.add('overlay');

    overlay.onclick = () => { // para que tambien cierrre tocando en cualquier lado !
        const body = document.querySelector('body');
        body.classList.remove('fijar-body');// para no permitir el scroll
        overlay.remove(); // remove es metodo de js
    }
    // boton para cerrar el modal:
    var cerrarModal = document.createElement('P');
    cerrarModal.textContent = 'X';
    cerrarModal.classList.add('btn-cerrar');
    cerrarModal.onclick = function () {
        const body = document.querySelector('body');
        body.classList.remove('fijar-body');// para no permitir el scroll
        overlay.remove(); // remove es metodo de js
    }
    overlay.appendChild(cerrarModal)

    // aniade al  html 
    const body = document.querySelector('body');
    body.appendChild(overlay);
    body.classList.add('fijar-body');// para no permitir el scroll
}
